"use client";

import React, { useEffect, useState } from "react";
import "@/styles/SellersScreen.scss";
import { useSession } from "next-auth/react";
import { fetchData } from "../../utils/api";

const SellersScreen = ({ sucursalId, collaborator }) => {
  const { data: session, status } = useSession();
  const [colaboradores, setColaboradores] = useState([]);

  /* const colaboradores = [
    { nombre: 'Juan P√©rez', tipo: 'Vendedor', transacciones: '$5,000', ubicacion: 'Ciudad A', telefono: '123-456-7890' },
    { nombre: 'Mar√≠a L√≥pez', tipo: 'Comprador', transacciones: '$3,200', ubicacion: 'Ciudad B', telefono: '987-654-3210' },
    { nombre: 'Carlos G√≥mez', tipo: 'Vendedor', transacciones: '$7,800', ubicacion: 'Ciudad C', telefono: '456-789-0123' },
  ]; */

  useEffect(() => {
    async function fetchCollaborators() {
      if (!session?.user) return;

      try {
        const res = await fetchData(
          `/users/${session.user.id}/sucursales/${sucursalId}/children`,
          "GET",
          null
        );

        setColaboradores(res);
        if (res.length === 0) {
            console.error("No se encontraron colaboradores.");
            }
        if (res.error) console.error("Error:", res.error);

      } catch (error) {
        console.error("Error cargando las sucursales:", error);
      }
    }
    

    fetchCollaborators();
  }, [session]);

  return (
    <div className="table-container">
      <h2>Colaboradores</h2>
      <table className="collaborators-table">
        <thead>
          <tr>
            <th>#</th>
            <th>Nombre</th>
            <th>Tipo</th>
            <th>Compras/Ventas ($)</th>
            <th>Ubicaci√≥n</th>
            <th>Tel√©fono</th>
          </tr>
        </thead>
        <tbody>
          {colaboradores.map((colaborador, index) => (
            <tr key={index} onClick={() => collaborator(colaborador)}>
              <td>{index + 1}</td>
              <td>{colaborador.userName}</td>
              <td>{colaborador.role}</td>
              <td>{colaborador.bought_sold}</td>
              <td><a href="#">Ver</a></td>
              <td>{colaborador.phone}</td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
};

export default SellersScreen;
